.blurWrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  visibility: hidden;
}

.formWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 20%;
  width: 35%;
  background-color: #eee;
}

.show {
  visibility: visible;
}

.quit {
  position: absolute;
  top: 0;
  right: 0;
}
